.App {
  background-color: #1D1E22;
  width: 100vw;
  height: 100vh;
}
.header {
  width: 100%;
  height: 100%;
}
nav {
  height: 12%;
  padding: 20px;
}
nav ul {
  height: 15%;
  margin: 0;
}
nav li {
  font-size: 16px;
  font-family: Arial;
  float: right;
  list-style-type: none;
  padding-right: 20px;
}
nav li a {
  text-decoration: none;
  color: #D4D4DC;
}
.childPage {
  min-height: 88%;
  position: relative;
}